body {
  margin-top: 2%;
  margin-left: 0px;
  margin-right: 0px;
  background: -webkit-gradient(linear, right top, left top, from(#4568dc), to(#b06ab3));
  background: -webkit-linear-gradient(right, #4568dc, #b06ab3);
  background: -o-linear-gradient(right, #4568dc, #b06ab3);
  background: linear-gradient(270deg, #4568dc, #b06ab3);
  background-size: 400% 400%;
  -webkit-animation: AnimationName 30s ease infinite;
  animation: AnimationName 30s ease infinite; }

@-webkit-keyframes AnimationName {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

@keyframes AnimationName {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

.logo {
  font-family: 'Dancing Script', cursive;
  font-size: 25px;
  color: #fff; }

.search-logo {
  font-family: 'Poiret One', cursive;
  font-family: 'Quicksand', sans-serif;
  text-align: center; }

.nav {
  font-family: 'Quicksand', sans-serif;
  font-size: 16px;
  color: #fff; }

.footer {
  font-family: 'Poiret One', cursive;
  font-family: 'Quicksand', sans-serif; }

@media screen and (min-width: 600px) {
  .search-logo {
    float: right; }
  .logo {
    margin-left: 0px; } }

@media screen and (min-width: 1225px) {
  .search-logo {
    float: right; }
  .logo {
    margin-left: 0px; } }



