html, body {
  height: 100%; }

#root {
  height: 100%; }

.App {
  height: 90%; }

.container {
  height: 100%;
  display: grid;
  grid-gap: 3px;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: 60px 60px auto 30px;
  grid-template-areas: "h" "s" "c" "f"; }

.header {
  grid-area: h;
  text-align: center; }

.nav {
  grid-area: s; }

.content {
  grid-area: c; }

.footer {
  grid-area: f; }

h2 {
  font-family: 'Dancing Script', cursive;
  font-family: 'Poiret One', cursive;
  font-family: 'Quicksand', sans-serif;
  font-size: 20px;
  max-width: 810px;
  margin-top: 10%;
  color: #fff;
  margin-left: 20px; }

p {
  font-family: 'Quicksand', sans-serif;
  color: #fff;
  font-size: 16px; }

@media screen and (min-width: 600px) {
  .container {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 60px auto 30px;
    margin-left: 20px;
    margin-right: 20px;
    grid-template-areas: "h s" "c c" "f f"; }
  h2 {
    font-size: 25px;
    margin-left: 0px; }
  .header {
    text-align: left; } }

@media screen and (min-width: 1225px) {
  .container {
    max-width: 1200px;
    margin: 0 auto;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 60px auto 30px;
    grid-template-areas: "h h h h h h h h h h s s" "c c c c c c c c c c c c" "f f f f f f f f f f f f"; }
  h2 {
    font-size: 35px;
    margin-left: 0px; }
  .header {
    text-align: left; } }



